.App {
  text-align: center;
  
}

* {
  box-sizing: border-box;
}
 
*, button, input {
  font-family: 'Roboto', sans-serif;
}

#espaco{
  width: 100px;
}

#botaoMenu {
  margin-left: 10px;
  border-radius: 17px;
  background-color: #FFFFFF;
  color: #717171;
  font-weight: bold;
  border: none;
  box-shadow: 0 0 4px #C8C8C8;
  height: 35px;
  width: 130px;
  
}

#botaoMenu:hover  {
  background-color: #C8C8C8;
}

#botaoHome {
  margin-left: 10px;
  border-radius: 17px;
  background-color: #FFFFFF;
  color: #FDC921;
  font-weight: bold;
  border: none;
  box-shadow: 0 0 4px #C8C8C8;
  height: 40px;
  width: 200px;
  
}

#botaoHome:hover  {
  background-color: #fae5a7;
}

.botoes {
  margin-left:10px;
  border-radius: 10px;
  color:#717171;
  background-Color:#FFFFFF;
  box-shadow: 0 0 1em #C8C8C8;
  border: 1px solid ;
  border-color: #FFFFFF;

}


nav {
   display: inline-flex;
   justify-content: space-between;
   align-items: center;
   padding: 0 10px;
   border-bottom: 1px solid;
   border-color: #C8C8C8;
   
}

.txtGold{
  color: #FDC921;
  cursor: pointer;
}


.NossosServicos {
  margin-top: 40px;
  text-align: center;
}

.boxServicos {
  position: relative;
  background-color:#FFFFFF;
  border-radius: 17px;  
  display: inline-flex;
  box-shadow: 0 0 1em #C8C8C8;
  
  
}

.card {
  box-shadow: 0 0 1em #C8C8C8;
  
}


.main-footer  li{
  color: #C8C8C8;
  
}

.TituloFooter  {
  color:#717171 ;  
}

.main-footer {
  
  padding-top: 2em;
  position: relative;
  bottom: 0;
  width: 100%;
  border-top: 1px solid;
  border-color: #C8C8C8;
  margin-top: 40px;
}

#botaoFooter {
  
  border-radius: 17px;
  background-color: #FFFFFF;
  color: #717171;
  font-weight: bold;
  border: none;
  box-shadow: 0 0 4px #C8C8C8;
  height: 35px;
  width: 160px;
  
}

#botaoFooter:hover  {
  background-color: #C8C8C8;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}